import React, { Component } from 'react';

import s from './AaboutCount';

class AaboutCount extends Component {
    formatNum() {
        let x = this.props.num;
        const rgx = /(\d+)(\d{3})/;
        while (rgx.test(x)) {
            x = x.replace(rgx, '$1' +
                ',' +
                '$2'
            );
        }
        const arr = [];
        for (let i = 0; i < x.length; i++) {
            if (x[i] !== ',') {
                arr.push(<li className={s.count} key={i}>{x[i]}</li>);
            } else {
                arr.push(<li className={s.default} key={i}>{x[i]}</li>);
            }
        }
        return arr;
    }
    render() {
        return (
            <div className={s.AaboutCount}>
                <h3>墨迹天气实时请求数</h3>
                <ul>
                    {this.formatNum()}
                </ul>
                <h3>墨迹天气API接口累计请求数</h3>
                <ul>
                    {this.formatNum()}
                </ul>
            </div>
        );
    }
}

export default AaboutCount;



