import React from 'react';
import { connect } from 'react-redux';

import Header from './../header';
import Nav from './../nav';
import Banner from './../banner';
import Anav from './../Anav';
import AaboutDetail from './../AaboutDetail';
import AaboutMap from './../AaboutMap';
import AaboutCount from './../AaboutCount';
import ToTop from './../toTop';
import s from './Aabout';

function Aabout(data) {
    const num = data.data.tobNum.data.data[0] ? data.data.tobNum.data.data[0].num : null;
    return (
        <div className={s.box}>
            <Header data={data.data.forecast} />
            <Nav />
            <Banner />
            <Anav router={data.route.path} />
            <AaboutDetail />
            <AaboutMap />
            <AaboutCount num={num} />
            <ToTop />
        </div>
    );
}

export default connect(
    state => ({
        data: state,
    })
)(Aabout);




